




















































































































@import "@/assets/scss/variables.scss";
article {
  h1 {
    font-size: 50px;
    line-height: 60px;
    @include breakpoint(mobile) {
      font-size: 24px;
      line-height: 24px;
    }
  }
  address {
    margin: 24px 0px;
    color: black;
    font-style: normal;
    font-weight: 400;
    font-size: 1rem;
    cursor: pointer;
  }
  time {
    display: block;
    margin: 24px 0px;
    color: black;
    font-style: normal;
    font-weight: 400;
    font-size: 1rem;
  }
  p {
    font-size: 18px;
    line-height: 28px;
  }
  h2 {
    cursor: pointer;
    text-transform: capitalize;
  }
  h3 {
    text-transform: capitalize;
  }
}
@media screen {
  article.document {
    background-color: white;
    padding: 30px;
    overflow-y: auto;
    overscroll-behavior-y: contain;
    @include breakpoint(mobile) {
      padding: 15px;
    }
    figure {
      margin-left: -30px;
      margin-right: -30px;
      margin-top: -30px;
      background-color: $primary-color;
      @include breakpoint(mobile) {
        margin-left: -15px;
        margin-right: -15px;
        margin-top: -15px;
      }
    }

    main {
      max-width: 800px;
      margin: auto;
    }

    .actions {
      position: fixed;
    }
  }
}

@media print {
  figure {
    margin: 0;
  }
}
