






















































































@import "@/assets/scss/variables.scss";
article {
  aside {
    margin-top: 24px;
    h1 {
      font-size: 2rem;
      line-height: 2.2rem;
      @include breakpoint(mobile) {
        font-size: 24px;
        line-height: 24px;
      }
    }

    h2 {
      font-size: 1.3rem;
      line-height: 1.4rem;
      text-transform: capitalize;
      @include breakpoint(mobile) {
        font-size: 16px;
        line-height: 16px;
      }
    }

    p {
      font-size: 18px;
      line-height: 28px;
    }

    time {
      display: block;
      margin: 24px 0px;
      color: black;
      font-style: normal;
      font-weight: 400;
      font-size: 1rem;
    }
  }
}
@media screen {
  article {
    background-color: $primary-color;
    overflow: hidden;
    .figure {
      cursor: zoom-out;
      height: 100%;
      width: 100%;
      &.small {
        cursor: zoom-in;
        height: 50%;
      }
    }
    aside {
      position: absolute;
      bottom: 0px;
      right: 0px;
      padding: 30px;
      overflow-y: auto;
      overscroll-behavior-y: contain;
      background-color: white;
      padding: 30px;
      @include breakpoint(mobile) {
        padding: 15px;
      }
      transition: transform .3s ease-in-out;
      max-height: 70%;
      max-width: $max-document-width;

      h1, h2 {
        cursor: pointer;
      }
    }
  }

  .slide-enter,
  .slide-leave-active {
    transform: translateX(100%);
  }
}

@media print {
  aside {
    display: block !important;
  }
}
